/* src/App.css */

body, html {
  margin: 0;
  padding: 0;
  height: 100%; /* Ensures the body takes the full height */
  overflow-x: hidden;
}

body {
  scroll-snap-type: y mandatory; /* Enable full-page snap scrolling */
  overflow-y: scroll; /* Make the body element the scroll container */
}

html {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

* {
  box-sizing: border-box;
}

.section {
  scroll-snap-align: start;
  width: 100vw;
  height: 100vh;
}

/* Define different background colors for each section */
#about {
  background-color: #E8F1F2;
}

#skills {
  background-color: #B3EFB2;
}

#experience {
  background-color: #31493C;
  color: #f9f7f7; /* Light text color for better contrast on the dark background */
}

#contact {
  background-color: #001A23;
  color: #f9f7f7; /* Light text color for better contrast on the dark background */
}

ul {
  list-style: none;
  padding: 0;
}